<template>
  <div class="delete-bar">

    <div class="num"> <i @click="changeAll" class="iconfont" :class="num==allNum?'active':' '"
                         v-html="num==allNum? '&#xe633;':'&#xe62e;'"> </i> 全选</div>
    <div @click="delet" class="delet">{{num?'删除商品('+num+')':'删除商品'}}</div>
  </div>
</template>

<script>
  export default {
    name: 'delet-bar',
    props: {
      allNum:{
        type:Number,
        default:0
      },
      num: {
        type: Number,
        default: 0
      }
    },
    methods:{
      changeAll(){
        this.$emit('changeAll')
      },
      delet(){
        this.$emit('delet')
      }
    }
  }
</script>

<style lang="stylus" scoped>
  @import "../../common/stylus/iconfont.styl"
  .delete-bar
    border-top 1px solid #e6e6e6
    position fixed
    height 3.063rem
    width 100%
    bottom 0
    left 0
    z-index 1000
    background-color #fff
    box-sizing border-box
    line-height 3.063rem
    display flex
    justify-content space-between
    padding 0 1rem
    align-items center
    .num
      font-size 0.813rem
      color #333
      i
        font-size 1.25rem
        vertical-align middle
        color #999
      .active
        color #ff4800
    .delet
      /*width: 5rem;*/
      padding 0 1rem
      height: 1.75rem;
      line-height 1.75rem
      text-align center
      font-size 0.813rem
      background-color: #ff4800;
      border-radius: 0.875rem;
      color #fff
</style>



