<template>
    <div class="no-result">
      <img :src="imgUrl" alt="">
      <p>{{title}}</p>
    </div>
</template>

<script>
  export default {
    name: 'no-result',
    props:{
      title:{
        type:String,
        default:''
      },
      imgUrl:{
        type:String,
        default:''
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  .no-result
    text-align center
    padding-top 9.531rem
    img
      width 9.188rem
    p
      padding-top 0.75rem
      font-size 0.75rem
      color #999
</style>



