<template>
  <div class="store">
    <div class="store-name">
      <i v-show="showChange" @click="changeAll" class="iconfont" :class="allChange?'active':' '" v-html="allChange? '&#xe633;':'&#xe62e;'"></i>袋鼠自营商城全场包邮
    </div>
    <div class="store-item" v-for="(item,index) in data">
      <div class="item-change">
        <i v-show="showChange" class="iconfont" @click="change(index)" :class="item.change?'active':' '" v-html="item.change? '&#xe633;':'&#xe62e;'"></i>
      </div>
      <div class="item-img">
        <img
          :src="item.cover"
          alt="">
      </div>
      <div class="item-info">
        <p class="title">{{item.title}}</p>
        <p class="type">规格：{{item.specValueStr}}</p>
        <p class="price">{{priceTxt(item.singleMinPayTb,item.singleCashPrice)}}</p>
        <p class="num-wrapper">原价：{{item.singleOldPrice}}</p>
        <shop-car-num @minus="minus(index)" @add="add(index)" :num="item.num" class="shop-num"></shop-car-num>
      </div>
    </div>
  </div>
</template>

<script>
  import ShopCarNum from '../../base/shop-car-num/shop-car-num'
  import tool from '../../common/js/util'

  export default {
    name: 'store',
    props:{
      data:{
        type:Array,
        default:[]
      },
      allChange:{
        type:Boolean,
        default:false
      },
      showChange:{
        type:Boolean,
        default:true
      }
    },
    methods:{
      add(index){
        this.$emit('add',index);
        this.$forceUpdate()
      },
      minus(index){
      this.$emit('minus',index);
        this.$forceUpdate()
      },
      priceTxt(tb,rmb){
        console.log(tb,rmb)
        return tool.priceStr(tb,rmb)
      },
      change(index){
        this.$emit('change',index);
        this.$forceUpdate()
      },
      changeAll(){
        this.$emit('changeAll')
      }
    },
    components: {
      ShopCarNum
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../common/stylus/iconfont.styl"
  .store
    padding-bottom 1.5rem
    background-color #fff
    .store-name
      padding 1.118rem 4.27%
      font-size 0.813rem
      color #000
      .active
        color #ff4800
      i
        font-size 1.25rem
        color #999
        vertical-align middle
        padding-right 0.625rem
    .store-item
      margin-bottom 1.5rem
      height 5.656rem
      box-sizing border-box
      padding 0 4.27%
      display flex
      justify-content left
      .item-change
        .active
          color #ff4800
        i
          font-size 1.25rem
          color #999
          line-height 5.656rem
          margin-right 0.625rem
      .item-img
        width 5.656rem
        height 5.656rem
        margin-right 0.625rem
        img
          width 100%
          height 100%
      .item-info
        max-width 13.282rem
        position relative
        display flex
        flex-direction column
        font-size 0.813rem
        color #333
        justify-content space-between
        .title
          line-height 1rem
        .type
          color #666
        .price
          color #ff4800
        .num-wrapper
          font-size 0.75rem
          text-decoration line-through #333
          padding-bottom 0.281rem
        .shop-num
          position absolute
          bottom 0
          left 7.25rem


</style>



