import CONFIG from './config'
import axios from 'axios'
import qs from 'qs'

/*
* 根据
* */
export function shopCar() {
  return axios.post(CONFIG.API.SHOP_CAR).then(res => res.data)
}

/*
* 根据
* */
export function upNum(opts) {
  return axios.post(CONFIG.API.UP_NUM, qs.stringify({
    sku:opts.sku,
    num:opts.num
  })).then(res => res.data)
}

/*
* 根据
* */
export function deletShop(opts) {
  return axios.post(CONFIG.API.DELET_SHOP, qs.stringify({
    skus:opts.skus
    })).then(res => res.data)
}



