<template>
  <div class="shop-car-num">
    <div @click="minus" class="minus">-</div><div>{{num}}</div><div @click="add" class="add">+</div>
  </div>
</template>

<script>
  export default {
    name: 'shop-car-num',
    props:{
      num:{
        type:Number,
        default:1
      }
    },
    methods:{
      minus(){
        this.$emit('minus')
      },
      add(){
        this.$emit('add')
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
@import "../../common/stylus/mixin.styl"
  .shop-car-num
    width 5.125rem
    border 1px solid #999
    display flex
    box-sizing border-box
    border-radius 3px
    div
      flex 2
      line-height 1.281rem
      text-align center
      font-size 0.75rem
      font-weight bold
      color #333
    .minus
      extend-click()
      flex 1
      align-items center
      border-right 1px solid #999
      color #666
    .add
      flex 1
      align-items center
      extend-click()
      border-left 1px solid #999
      color #666
</style>



