<template>
  <div class="close-bar">
    <div class="num"> <i @click="changeAll" class="iconfont" :class="num==allNum?'active':' '"
                         v-html="num==allNum? '&#xe633;':'&#xe62e;'"> </i> 全选</div>

    <div @click="close" class="delet" :style="num===0?'background-color:#999':'background-color:#ff4800'">{{num?'结算('+num+')':'结算'}}</div>
    <div class="price">合计: <span>{{price}}</span></div>
  </div>
</template>

<script>
  export default {
    name: 'delet-bar',
    props: {
      allNum:{
        type:Number,
        default:0
      },
      num: {
        type: Number,
        default: 0
      },
      price:{
        type:String,
        default:''
      }
    },
    methods:{
      changeAll(){
        this.$emit('changeAll')
      },
      close(){
        this.$emit('close')
      }
    }
  }
</script>

<style lang="stylus" scoped>
  @import "../../common/stylus/iconfont.styl"
  .close-bar
    border-top 1px solid #e6e6e6
    position fixed
    height 3.063rem
    width 100%
    bottom 0
    z-index 1000
    background-color #fff
    left 0
    box-sizing border-box
    line-height 3.063rem
    display flex
    justify-content space-between
    padding 0 0 0 1rem
    align-items center
    .num
      font-size 0.813rem
      color #333
      i
        font-size 1.25rem
        vertical-align middle
        color #999
      .active
        color #ff4800
    .delet
      width: 5.313rem;
      height 100%
      line-height 3.063rem
      text-align center
      font-size 1rem
      background-color: #ff4800;
      color #fff
    .price
      position absolute
      right 6rem
      font-size 0.813rem
      span
        color #ff4800
</style>



