<template>
  <div class="shop-car">
    <!--<shop-header title="购物车"></shop-header>-->
    <div class="shop-car-title" v-show="shopList.length>0">
      <span>共{{shopList.length}}件商品</span><span class="manage" @click="changeMode">{{mode===0?'管理':'完成'}}</span>
    </div>
    <scroll class="car-scroll" v-show="shopList.length>0">
      <div>
        <store @minus="minus" @add="add" @changeAll="changeAll"
               :all-change="allChange" @change="change"
               :data="shopList"></store>
      </div>
    </scroll>
    <delet-bar @changeAll="changeAll" v-show="mode===1&&shopList.length>0"
               @delet="delet" :all-num="shopList.length"
               :num="num"></delet-bar>
    <close-bar @close="close" @changeAll="changeAll" :num="num" :all-num="shopList.length" :price="price"
               v-show="mode===0&&shopList.length>0"></close-bar>
    <load-server v-show="showLoad"></load-server>
    <no-result v-show="shopList.length<=0 && showInit" :img-url="img" title="您的购物车空空如也~"></no-result>
  </div>
</template>

<script>
  // import ShopCarNum from '../../base/shop-car-num/shop-car-num.vue'
  import tool from '../../common/js/util'
  import ShopHeader from '../../base/shop-header/shop-header'
  import LoadServer from '../../base/load-server/load-server'
  import DeletBar from '../../base/delet-bar/delet-bar'
  import NoResult from '../../base/no-result/no-result'
  import CloseBar from '../../base/close-bar/close-bar'
  import Scroll from '../../base/scroll/scroll'
  import Store from '../../base/store/store'
  import * as core from '../../api/shopCar'
  import img from './images/no_shop.png'

  export default {
    name: 'shop-car',
    data() {
      return {
        shopList: [],
        allChange: false,
        num: 0,
        mode: 0,
        price: '￥0',
        showLoad: false,
        img:img,
        showInit:false
      }
    },
    components: {
      // ShopCarNum,
      ShopHeader,
      Scroll,
      Store,
      DeletBar,
      CloseBar,
      LoadServer,
      NoResult
    },
    created() {
      document.title = this.$route.meta.title     // console.log(this.$route.query.cartIds)
      this.getShopCar()
    },
    methods: {
      getUpNum(opts, isAdd, index) {
        this.showLoad = true;
        core.upNum(opts).then(res => {
          if (res.code && '00' === res.code) {
            this.showLoad = false
            if (isAdd) {
              this.shopList[index].num = this.shopList[index].num + 1
            } else {
              this.shopList[index].num = this.shopList[index].num - 1
            }
            let price = 0
            let priceTb = 0
            for (var i = 0; i < this.shopList.length; i++) {
              if (this.shopList[i].change) {
                price = price + (this.shopList[i].singleCashPrice) * this.shopList[i].num
                priceTb = priceTb + (this.shopList[i].singleMinPayTb) * this.shopList[i].num
              }
              if (price === 0 && priceTb === 0) {
                this.price = '￥0'
              } else {
                this.price = tool.priceStr(priceTb, price)
              }
            }
          }else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
          } else {
            this.$toastBox.showToastBox(res.message)
          }
        }).catch(e => {
          this.$toastBox.showToastBox(e)
        })
      },
      minus(index) {
        if (this.showLoad) {
          return false
        }
        if (this.shopList[index].num <= 1) {
          this.$toastBox.showToastBox('购买数量不能小于1')
          return false
        }
        this.getUpNum({
          sku: this.shopList[index].sku,
          num: this.shopList[index].num - 1
        }, false, index)

      },
      add(index) {
        if (this.showLoad) {
          return false
        }
        this.getUpNum({
          sku: this.shopList[index].sku,
          num: this.shopList[index].num + 1
        }, true, index)
      },
      changeMode() {
        if (this.mode === 0) {
          this.mode = 1
        } else {
          this.mode = 0
        }
      },
      close(){
        let deletStr = [];
        this.shopList.filter((currentValue, index, arr) => {
          if (currentValue.change) {
            deletStr.push(currentValue.id)
          }
        });
        if (deletStr.length>0){
          console.log(deletStr);
          this.$router.push({path:'/purchaseNow',query:{cartIds:deletStr.join(',')}})
          // shop/order/find/cart/details?cartIds=1,2,3
        }
      },
      delet() {
        let deletStr = [];
        this.shopList.filter((currentValue, index, arr) => {
          if (currentValue.change) {
            deletStr.push(currentValue.sku)
          }
        });
        if (deletStr.length>0){
          core.deletShop({skus: deletStr.join(',')}).then(res => {
            if (res.code && '00' === res.code) {
              this.shopList = this.shopList.filter((currentValue, index, arr) => {
                return !currentValue.change
              });
              this.price = '￥0'
              this.num=0
            }else if (res.code && '01' === res.code) {
              tool.toWeiXinLogin(window.location.href)
            } else {
              this.$toastBox.showToastBox(res.message)
            }
          }).catch(e => {
            this.$toastBox.showToastBox(e)
          })
        }
      },
      changeAll() {
        let price = 0
        let priceTb = 0
        this.allChange = !this.allChange;
        if (this.allChange) {
          this.num = this.shopList.length
          for (var i = 0; i < this.shopList.length; i++) {
            price = price + (this.shopList[i].singleCashPrice) * this.shopList[i].num
            priceTb = priceTb + (this.shopList[i].singleMinPayTb) * this.shopList[i].num
            this.price = tool.priceStr(priceTb, price)
          }
        } else {
          this.price = '￥0'
          this.num = 0
        }
        for (var i = 0; i < this.shopList.length; i++) {
          this.shopList[i].change = this.allChange
        }
      },
      change(index) {
        let price = 0
        let priceTb = 0
        this.num = 0
        this.allChange = false
        this.shopList[index].change = !this.shopList[index].change
        this.$forceUpdate()
        let count = 0;
        for (var i = 0; i < this.shopList.length; i++) {
          if (this.shopList[i].change) {
            price = price + (this.shopList[i].singleCashPrice) * this.shopList[i].num
            priceTb = priceTb + (this.shopList[i].singleMinPayTb) * this.shopList[i].num
            count++
            this.num = count
          }
          this.price = tool.priceStr(priceTb, price)
        }
        if (count === this.shopList.length) {
          this.allChange = true
        }
      },
      getShopCar() {
        core.shopCar().then(res => {
          if (res.code && '00' === res.code) {
            this.shopList = res.result.cartList;
            this.showInit=true
            for (var i = 0; i < this.shopList.length; i++) {
              this.shopList[i].change = false
            }
            console.log(this.shopList)
          }else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
          } else {
            this.$toastBox.showToastBox(res.message)
          }
        }).catch(e => {
          this.$toastBox.showToastBox(e)
        })
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../common/stylus/mixin.styl"
  .shop-car
    .shop-car-title
      height 2.031rem
      background-color #f5f5f5
      padding 0 4.27%
      margin 0 auto
      display flex
      font-size 0.813rem
      color #333
      justify-content space-between
      line-height 2.031rem
      .manage
        extend-click()
        color #42b0e9
    .car-scroll
      background-color #f5f5f5
      position fixed
      z-index -1
      top 2.031rem
      /*top 4.781rem*/
      left 0
      right 0
      bottom 3.063rem
</style>



